import * as R from 'ramda';
import 'rc-datetime-picker/dist/picker.css';
import { memo, useCallback, useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { bcat, useBlueprintComponentsContext } from '../../../../context/blueprintsComponents.context';
import { setMessageBar, setSelectedProStep } from '../../../../reduxStore/actions/flowActions';
import { createBlueprintComponent, deleteBlueprintComponent, duplicateComponentBlueprint, updateBlueprintComponent } from '../../../../services/components.service';
import { fileExtensions, groupings } from '../../../../shared/utility';
import { Button, ModalR, TutorialRectangle, PermissionWrapper, po, RenderTitle } from '../../../common';
import { SingleSelectWithCheckboxesAndNestedItems } from '../../../common/UI/Select/MultiSelectWithCheckboxesAndNestedItems';
import cls from './ComponentsModal.module.scss';
import { FieldOptionsModal, OptionSection, RenderTutorialRectangle } from './FieldOptionsModal';
import { isMobile } from 'react-device-detect';
import MessageContainer from '../../ProcessesComps/MessageContainer/MessageContainer';
import { store } from '../../../../reduxStore/store';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronDown, faGear } from '@fortawesome/free-solid-svg-icons';
import { TutorialBits } from '../../../OnboardingEntries/OnboardingModals/OnBoardingModals';
import { setOnBoardingModalsVisibility } from '../../../../reduxStore/actions/modalsActions';

const ComponentsModal = memo(props => {
	const { visible, onUpdate, onClose, hasTutorial, allModules, blueprints, componentTypes, components, products, companyData, companyProcesses, selectedComponent } = props;
	const [bpContext, setBpContext] = useBlueprintComponentsContext();
	const [showTutorial, setShowTutorial] = useState(false);
	const [optionsModalVisible, setOptionsModalVisible] = useState(false);
	const [loading, setLoading] = useState(false);
	const [dLoading, setDLoading] = useState(false);
	const [processingUpdate, setProcessingUpdate] = useState(false);
	const [processingDelete, setProcessingDelete] = useState(false);
	const [, setSectionErrors] = useState({ name: '', fields: '', components: '' });
	const [showErrors, setShowErrors] = useState(false);
	const [generalError, setGeneralError] = useState('');
	const [isNew, setIsNew] = useState(true);
	const [isRename, setIsRename] = useState(false);
	const [templateMessage, setTemplateMessage] = useState('');

	const { onBoardingModalsVisibility, isOnboarding } = props;


	const {
		firstModalVisible,
		secondModalVisible,
		thirdModalVisible,
		fourthModalVisible,
		fifthModalVisible,
		sixthModalVisible,
		seventhModalVisible,
		eightModalVisible,
		ninthModalVisible,
		tenthModalVisible,
		eleventhModalVisible,
		twelvthModalVisible,
		lastModalVisible,
		positionModalVisible,
		thirteenthModalVisible,
	} = onBoardingModalsVisibility;

	useEffect(() => {
		setGeneralError('');
		setBpContext({
			type: bcat.setAllBlueprintsComponents,
			value: blueprints,
		});
	}, [bpContext.selectedBlueprintComponent.fields]);

	useEffect(() => {
		if (selectedComponent !== undefined) {
			setBpContext({ type: bcat.setSelectedBlueprintComponent, value: blueprints.find(c => c.id === selectedComponent) });
			setIsNew(false);
		}
	}, []);

	const onDeleteComponentBlueprint = () => {
		setDLoading(true);
		deleteBlueprintComponent(bpContext.selectedBlueprintComponent.id).subscribe({
			next: () => {
				store.dispatch(
					setMessageBar({
						show: true,
						message: `You have successfully deleted ${bpContext.selectedBlueprintComponent.name}`,
						color: 'red',
					})
				);
				onUpdate?.(true);
			},
			complete: () => {
				setDLoading(false);
				onClose?.(true);
			},
		});
	};
	const resetErrors = () => {
		setSectionErrors({ name: '', components: '' });
		setGeneralError('');
	};

	const callUpdateComponentBlueprint = useCallback(
		payload => {
			setLoading(true);
			updateBlueprintComponent(payload.id, payload).subscribe({
				next: () => {
					setLoading(false);
					store.dispatch(
						setMessageBar({
							show: true,
							message: `You have successfully updated ${payload.name}`,
						})
					);
					onUpdate?.(true);
					onClose?.(true);
				},
				error: error => {
					if (error?.data?.errors) {
						setSectionErrors({
							name: error?.data?.errors?.name?.join('; '),
							components: error?.data?.errors?.components?.join('; '),
						});
					}
					if (error?.data) {
						setGeneralError(error?.data?.message ?? error?.data);
					}
					setLoading(false);
				},
			});
		},
		[onClose]
	);

	const callCreateComponentBlueprint = useCallback(
		payload => {
			setLoading(true);
			createBlueprintComponent(payload).subscribe({
				next: () => {
					
					if (onBoardingModalsVisibility?.sixthModalVisible) {
						store.dispatch(setOnBoardingModalsVisibility({ seventhModalVisible: true }));
					} else if (onBoardingModalsVisibility?.seventhModalVisible) {
						store.dispatch(setOnBoardingModalsVisibility({ eightModalVisible: true }));
					} 

					store.dispatch(setMessageBar({ show: true, message: `You have successfully created ${payload.name}` }));
					setLoading(false);
					onUpdate?.(true);
					onClose?.(true);
				},
				error: error => {
					if (error?.data?.errors) {
						setSectionErrors({
							name: error?.data?.errors?.name?.join('; '),
							components: error?.data?.errors?.components?.join('; '),
						});
					}
					if (error?.data) {
						setGeneralError(error?.data?.message ?? error?.data);
					}
					setLoading(false);
				},
			});
		},
		[onClose]
	);
	const callDuplicateComponentBlueprint = useCallback(
		payload => {
			setLoading(true);
			duplicateComponentBlueprint(payload.id, payload.name).subscribe({
				next: () => {
					setLoading(false);
					store.dispatch(
						setMessageBar({
							show: true,
							message: `You have successfully duplicated component`,
						})
					);
					onUpdate?.(true);
					onClose?.(true);
				},
				error: error => {
					if (error?.data?.errors) {
						setSectionErrors({
							name: error?.data?.errors?.name?.join('; '),
							components: error?.data?.errors?.components?.join('; '),
						});
					}
					if (error?.data) {
						setGeneralError(error?.data?.message ?? error?.data);
					}
					setLoading(false);
				},
			});
		},
		[onClose]
	);
	const onNewBlueprintComponentPressed = () => {
		setIsNew(true);
		setBpContext({ type: bcat.resetSelectedBlueprintComponent });
	};
	const onSaveBlueprintComponentPressed = useCallback(() => {
		const { name, fields } = bpContext.validation;
		if (name || fields?.length > 0 || bpContext.selectedBlueprintComponent.fields?.length < 1) {
			let fieldErrorMessage = '';
			if (bpContext.selectedBlueprintComponent.fields?.length < 1) fieldErrorMessage = 'Component must have at least one field';
			if (fields?.length > 0) fieldErrorMessage = 'Check options of red fields';

			/* nested options valiadation */
			if (bpContext?.selectedBlueprintComponent?.fields?.some(i => i?.options?.list?.length == 0)) fieldErrorMessage = "Element's option list must have at least one entry";
			if (bpContext?.selectedBlueprintComponent?.fields?.some(i => i?.options?.type?.length == 0)) fieldErrorMessage = "Element's option type is mandatory";
			if (bpContext?.selectedBlueprintComponent?.fields?.some(i => i?.value?.length == 0)) fieldErrorMessage = 'Name is mandatory for each element';

			setSectionErrors({
				name: name ? 'Add a component name' : '',
				fields: fieldErrorMessage,
			});
			if (fieldErrorMessage?.length > 0) {
				const errorFields = document.querySelectorAll('[app-field-row-has-error="error"]');
				if (errorFields) {
					if (errorFields?.length > 0) {
						[...errorFields]?.reverse()[errorFields?.length - 1]?.scrollIntoView({
							block: 'start',
							behavior: 'smooth',
						});
					}
				}
			}
			if (fields?.length > 0) {
				setShowErrors(true);
			}
			return;
		}
		setLoading(true);
		resetErrors();
		const payload = {
			components: bpContext.selectedBlueprintComponent.components,
			fields: bpContext.selectedBlueprintComponent.fields.map(i => (i.id?.length > 12 ? { ...i } : R.dissoc('id')(i))),
			name: bpContext.selectedBlueprintComponent.name,
		};
		if (bpContext.selectedBlueprintComponent.id?.length > 0) {
			callUpdateComponentBlueprint({ ...payload, id: bpContext.selectedBlueprintComponent.id });
		} else {
			callCreateComponentBlueprint(payload);
		}
	}, [bpContext.selectedBlueprintComponent.components, bpContext.selectedBlueprintComponent.fields, bpContext.selectedBlueprintComponent.id, bpContext.selectedBlueprintComponent.name, bpContext.validation, callCreateComponentBlueprint, callUpdateComponentBlueprint]);

	const onDuplicateBlueprintComponentPressed = useCallback(() => {
		if (bpContext.selectedBlueprintComponent.id?.length > 0) {
			callDuplicateComponentBlueprint({ name: bpContext.selectedBlueprintComponent.name, id: bpContext.selectedBlueprintComponent.id });
		}
	}, [bpContext.selectedBlueprintComponent.components, bpContext.selectedBlueprintComponent.fields, bpContext.selectedBlueprintComponent.id, bpContext.selectedBlueprintComponent.name, bpContext.validation, callCreateComponentBlueprint, callUpdateComponentBlueprint]);

	const updateNameInContext = newName => {
		setBpContext({ type: bcat.setSelectedBlueprintComponentName, value: newName });
	};

	const onDeleteSelectedComponentBlueprintSelectedField = fieldId => {
		setProcessingDelete(true);
		setBpContext({ type: bcat.removeSelectedFieldFromSelectedBlueprintComponent, value: { id: fieldId } });
		setProcessingDelete(false);
		setOptionsModalVisible(false);
	};
	const onSaveComponentField = args => {
		setProcessingUpdate(true);
		setBpContext({ type: bcat.setSelectedBlueprintComponentSpecificField, value: args });
		setProcessingUpdate(false);
		setOptionsModalVisible(false);
	};
	const onOptionsModalClose = () => {
		setBpContext({ type: bcat.setSelectedFieldFromSelectedBlueprintComponent, value: null });
		setProcessingUpdate(false);
		setProcessingDelete(false);
		setOptionsModalVisible(false);
	};

	const checkCustomTemplatesForComponentModal = (selection, companyData, componentTypes) => {
		if (selection[0].value === 'Battery Order Template') {
			return companyData?.misc_info?.templatesData
				.find(template => template.name === 'Battery Order Template')
				.config.map(temp => {
					return { value: componentTypes.find(ct => ct.name === temp.value).id, label: temp.label, mapToItem: temp?.mapToItem };
				});
		} else if (selection[0].value === 'Battery Template') {
			return companyData?.misc_info?.templatesData
				.find(template => template.name === 'Battery Template')
				.config.map(temp => {
					return { value: componentTypes.find(ct => ct.name === temp.value).id, label: temp.label, mapToItem: temp?.mapToItem };
				});
		} else {
			return (
				companyData?.misc_info?.templatesData
					.find(template => template.name === selection[0].value)
					?.config?.map(temp => {
						return { value: componentTypes.find(ct => ct.name === temp.value).id, label: temp.label, mapToItem: temp?.mapToItem };
					}) ?? []
			);
		}
	};

	return (
		<>
			<ModalR id="edit-component-modal" blured={true} scrollable={false} isOpen={visible} shadowedContainerStyle={{ minWidth: '500px', width: isMobile ? '100%' : '38%' }}>
				<div className={cls.host}>
					<div className={cls.title}>
						<p>{bpContext?.selectedBlueprintComponent?.readonly == true ? 'Template Component' : isNew ? 'New Component' : 'Edit Component'}</p>
					</div>
					<p className={cls.fieldType}>{'Note: You can only use a component one step at a time in a workflow'}</p>

					<OptionSection>
						{isNew && (
							<div>
								<RenderTitle title={'Name'} />
								<input
									type="text"
									name="element-name"
									value={bpContext.selectedBlueprintComponent?.name}
									onChange={e => {
										updateNameInContext(e.target.value);
									}}
									onClick={() => { }}
									className={cls.elementTextInput}
								/>
							</div>
						)}
						{isRename && (
							<div>
								<RenderTitle title={'Name'} />
								<input
									type="text"
									name="element-name"
									value={bpContext.selectedBlueprintComponent?.name}
									onChange={e => {
										updateNameInContext(e.target.value);
									}}
									onClick={() => { }}
									className={cls.elementTextInput}
								/>
							</div>
						)}

						{!isRename && (
							<div style={{ display: 'flex', flexDirection: 'column', width: '100%', maxHeight: '100%', border: '0px solid var(--app-color-gray-lighter)' }}>
								<RenderTitle title={selectedComponent ? 'Name' : 'Select Component to Edit'} />
								<div className={cls.fieldRow}>
									<SingleSelectWithCheckboxesAndNestedItems
										placeholder={bpContext.selectedBlueprintComponent?.name && !isNew ? bpContext.selectedBlueprintComponent?.name : 'Or selelct a Component to EDIT...'}
										items={
											selectedComponent
												? []
												: componentTypes
													? [
														...R.sortWith([R.ascend(R.prop('name'))])(blueprints).map(component => {
															return { label: `${component?.name}`, value: component?.id, type: `${component.readonly === true ? 'Templates' : groupings(component?.name)}` };
														}),
													]
													: []
										}
										initialSelectedValues={[]}
										groupedBy={'type'}
										groupname={'type'}
										inputStyle={{ blank: true, cursor: 'pointer' }}
										className={cls.peoplePositionsSelect}
										inputClassName={cls.peoplePositionsSelectInput}
										molclassname={cls.peoplePositionsSelectMenuOverlay}
										moliclassname={cls.peoplePositionsSelectMenuOverlayItem}
										onChange={selection => {
											if (selection.length == 1) {
												setBpContext({ type: bcat.setSelectedBlueprintComponent, value: blueprints.find(c => c.id === selection[0].value) });
												setIsNew(false);
											}
										}}
									>
										{!selectedComponent && <FontAwesomeIcon className="fa-icon" icon={faChevronDown} style={{ marginRight: '20px', color: 'gray' }} />}
									</SingleSelectWithCheckboxesAndNestedItems>
								</div>
							</div>
						)}
						<br />

						{!isRename && (
							<div style={{ display: 'flex', flexDirection: 'column', width: '100%', maxHeight: '100%', border: '0px solid var(--app-color-gray-lighter)' }}>
								<RenderTitle title={'Fields Selection'} />
								<div className={cls.fieldRow}>
									<SingleSelectWithCheckboxesAndNestedItems
										placeholder={'Click and select field to add ...'}
										items={
											componentTypes
												? [
													...R.sortWith([R.ascend(R.prop('name'))])(componentTypes.filter(type => !(companyData?.misc_info?.blueprints?.includes(type?.name)) && (type.io_type == 0 || type.io_type == 1))).map(component => {
														return { label: component.name, value: component.id, type: component.io_type == 0 ? 'Output' : 'Input' };
													}),
													...(companyData?.misc_info?.templates
														? companyData.misc_info.templates.map(template => {
															return { label: template, value: template, type: 'Templates' };
														})
														: []),
												]
												: []
										}
										initialSelectedValues={[]}
										groupedBy={'type'}
										groupname={'type'}
										inputStyle={{ blank: true, cursor: 'pointer' }}
										className={cls.peoplePositionsSelect}
										inputClassName={cls.peoplePositionsSelectInput}
										molclassname={cls.peoplePositionsSelectMenuOverlay}
										moliclassname={cls.peoplePositionsSelectMenuOverlayItem}
										onChange={selection => {
											if (selection.length > 0) {
												let selections = [selection[0]];

												selections = checkCustomTemplatesForComponentModal(selection, companyData, componentTypes);
												if (selections.length == 0) {
													setTemplateMessage('');
													selections = [selection[0]];
												}

												selections.map(sel => {
													const field = componentTypes.find(ct => ct.id === sel.value);
													let addingField = { field_type: { ...field } };
													if (field?.name?.toLowerCase() === 'number') {
														addingField = {
															field_type: { ...field },
															options: { type: 'numeric', display: 'text' },
														};
													}
													if (field?.name?.toLowerCase() === 'phone') {
														addingField = {
															field_type: { ...field },
															options: { country_prefix: '+44', country_code: 'gb', country_name: 'United Kingdom' },
														};
													}
													if (field?.name?.toLowerCase() === 'currency') {
														addingField = {
															field_type: { ...field },
															options: { currency_type: 'British Pound Sterling', currency_value: 'GBP', symbol: '£' },
														};
													}
													if (field?.name?.toLowerCase() === 'action button') {
														addingField = {
															field_type: { ...field },
															options: { action: 'creator' },
														};
													}
													if (field?.name?.toLowerCase() === 'profile') {
														addingField = {
															field_type: { ...field },
															options: { type: 'user' },
														};
													}
													if (field?.name?.toLowerCase() === 'upload') {
														addingField = {
															field_type: { ...field },
															options: {
																accepted_mimes: fileExtensions.map(i => i.value?.replaceAll('.', '')).join(','),
																max_file_size: 50 * 1024,
															},
														};
													}
													if (field?.name?.toLowerCase() === 'people') {
														addingField = {
															field_type: { ...field },
															options: { position_id: [], type: 'single' },
														};
													}
													if (field?.name?.toLowerCase() === 'date') {
														addingField = {
															field_type: { ...field },
															options: { type: 'single' },
														};
													}
													if (field?.name?.toLowerCase() === 'dropdown') {
														addingField = {
															field_type: { ...field },
															options: {
																list: [],
																type: 'single',
															},
														};
													}
													if (field?.name?.toLowerCase() === 'payments') {
														addingField = {
															field_type: { ...field },
															options: {
																list: ['Credit Card', 'GCash', 'Cash'],
															},
														};
													}
													if (field?.name?.toLowerCase() === 'pin') {
														addingField = {
															field_type: { ...field },
															options: {
																pintype: 'single',
																realtime: true,
																destination: false,
																source: true,
															},
														};
													}
													if (field?.name?.toLowerCase() === 'record') {
														addingField = {
															field_type: { ...field },
															options: {
																component_id: [],
																type: 'single',
																selection: '',
																filters: [],
															},
														};
													}
													if (field?.name?.toLowerCase() === 'status') {
														addingField = {
															field_type: { ...field },
															options: {
																list: [],
															},
														};
													}
													if (field?.name?.toLowerCase() === 'list filter') {
														addingField = {
															field_type: { ...field },
															options: {
																field: '',
																value: '',
															},
														};
													}
													if (field?.name?.toLowerCase() === 'id') {
														addingField = {
															field_type: { ...field },
															options: {
																id: '',
																value: 0,
															},
														};
													}
													if (field?.name?.toLowerCase() === 'list') {
														addingField = {
															field_type: { ...field },
															options: {
																module_id: '',
																list: [],
															},
														};
													}
													if (field?.name?.toLowerCase() === 'module list') {
														addingField = {
															field_type: { ...field },
															options: {
																module_id: '',
																list: [],
															},
														};
													}
													if (field?.name?.toLowerCase() === 'step list') {
														addingField = {
															field_type: { ...field },
															options: {
																component_id: '',
																list: [],
															},
														};
													}
													if (field?.name?.toLowerCase() === 'component list') {
														addingField = {
															field_type: { ...field },
															options: {
																component_id: '',
																list: [],
															},
														};
													}
													if (field?.name?.toLowerCase() === 'transaction list') {
														addingField = {
															field_type: { ...field },
															options: {
																map: {},
															},
														};
													}
													if (field?.name?.toLowerCase() === 'lookup') {
														addingField = {
															field_type: { ...field },
															options: {
																source_id: '',
																target_id: '',
																common_field: '',
																list: [],
																selection: '',
															},
														};
													}
													if (field?.name?.toLowerCase() === 'rewards') {
														addingField = {
															field_type: { ...field },
															options: {
																status: 'Closed',
																points: 1,
																equiv: 1,
																creator: true,
															},
														};
													}
													if (field?.name?.toLowerCase() === 'calendar') {
														addingField = {
															field_type: { ...field },
															options: {
																limit: true,
																creator: true,
																priority: true,
																span: false
															},
														};
													}
													if (field?.name?.toLowerCase() === 'connectors') {
														addingField = {
															field_type: { ...field },
															options: {
																component1_id: '',
																component2_id: '',
																common_field_12: '',
																component3_id: '',
																common_field_23: '',
																outputs: '',
															},
														};
													}
													if (field?.name?.toLowerCase() === 'map') {
														addingField = {
															field_type: { ...field },
															options: {
																maptype: 'region',
															},
														};
													}
													if (field?.name?.toLowerCase() === 'formula') {
														addingField = {
															field_type: { ...field },
															options: { operator: '', list: [], display: 'text' },
														};
													}
													if (field?.name?.toLowerCase() === 'emails') {
														addingField = {
															field_type: { ...field },
															options: { subject: '', message: '' },
														};
													}
													if (field?.name?.toLowerCase() === 'product') {
														addingField = {
															field_type: { ...field },
															options: { list: [] },
														};
													}
													if (field?.name?.toLowerCase() === 'product lookup') {
														addingField = {
															field_type: { ...field },
															options: {
																selection: '',
																component_id: '',
																list: [],
															},
														};
													}
													if (field?.name?.toLowerCase() === 'filters') {
														addingField = {
															field_type: { ...field },
															options: {
																component_id: '',
																list: [],
																filters: [],
															},
														};
													}
													if (field?.name?.toLowerCase() === 'info') {
														addingField = {
															field_type: { ...field },
															options: {
																component_id: '',
																list: [],
															},
														};
													}
													if (field?.name?.toLowerCase() === 'chart') {
														addingField = {
															field_type: { ...field },
															options: {
																component_id: '',
																list: [],
															},
														};
													}
													if (field?.name?.toLowerCase() === 'value') {
														addingField = {
															field_type: { ...field },
															options: {
																module_id: '',
																component_id: '',
																value: '',
															},
														};
													}
													if (field?.name?.toLowerCase() === 'notes') {
														addingField = {
															field_type: { ...field },
															options: {
																value: '',
															},
														};
													}
													if (field?.name?.toLowerCase() === 'url') {
														addingField = {
															field_type: { ...field },
															options: {
																type: 'image',
															},
														};
													}
													if (field?.name?.toLowerCase() === 'googleai assistant') {
														addingField = {
															field_type: { ...field },
															options: {
																description: '',
																fixed: true,
																current: false,
															},
														};
													}
													if (sel?.mapToItem) {
														console.log(sel.mapToItem)
														addingField.options = { ...addingField.options, mapToItem: sel.mapToItem };
													}
													if (bpContext?.selectedBlueprintComponent?.fields.length < 12) {
														resetErrors();
														setBpContext({
															type: bcat.setSelectedBlueprintComponentNewField,
															value: { ...addingField, label: sel.label ?? '' },
														});
													} else {
														setGeneralError('You can only add a maximum of 12 fields per component. ');
													}
												});
											}
										}}
									>
										<FontAwesomeIcon className="fa-icon" icon={faChevronDown} style={{ marginRight: '20px', color: 'gray' }} />
									</SingleSelectWithCheckboxesAndNestedItems>
								</div>

								<br />
								<div className={cls.selectedFieldsContainer}>{isNew ? 'Selected Fields:' : 'Available Fields:'}</div>
								<div style={{ display: 'flex', flexDirection: 'column' }}>
									{bpContext?.selectedBlueprintComponent?.fields?.map((item, index) => {
										return (
											<BPFieldRowWrapper
												key={index}
												id={item.id}
												name={item?.field_type?.io_type === 1 ? item?.options?.mapToItem ? `${item?.field_type?.name} (${item?.options?.mapToItem})` : item?.field_type?.name : item?.field_type?.name === 'Template' ? item?.field_type?.name : `${item?.field_type?.name} (Out)`}
												settingsClicked={() => {
													setBpContext({
														type: bcat.setSelectedFieldFromSelectedBlueprintComponent,
														value: { ...item, index: item?.field_type?.index },
													});
													setOptionsModalVisible(true);
												}}
												renderError={showErrors}
												hasSettingsBtn
											>
												<RenderFieldContent {...item} />
											</BPFieldRowWrapper>
										);
									})}
								</div>
								<p className={cls.selectedFieldsContainer}>{bpContext?.selectedBlueprintComponent?.fields?.length == 0 ? 'No fields available. Select and add by clicking above' : 'You can add a maximum of 12 fields per component.'}</p>
								<br />
							</div>
						)}

						{generalError?.length > 0 && <MessageContainer message={generalError} onClick={() => setGeneralError('')} />}
						{templateMessage?.length > 0 && <MessageContainer withError={false} extraClassName={cls.warningContainer} message={templateMessage} onClick={() => setTemplateMessage('')} />}
					</OptionSection>

					<div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', paddingTop: '10px', width: '100%', overflow: 'auto' }}>

						<Button loading={loading} title={'Close'} clicked={() => onClose()}
							titleStyle={{ fontSize: '14px' }}
							style={{
								width: '16%',
								display: isRename ? 'none' : ''
							}} />
						<Button
							disabled={bpContext?.selectedBlueprintComponent?.fields.length > 0 && bpContext.selectedBlueprintComponent?.name ? false : true}
							light
							title={isNew ? 'Save' : 'Update'}
							loading={loading}
							clicked={onSaveBlueprintComponentPressed}
							titleStyle={{ fontSize: '14px' }}
							style={{
								width: '20%',
								display: bpContext?.selectedBlueprintComponent?.readonly == true && companyData.status !== 'superadmin' ? 'none' : bpContext?.selectedBlueprintComponent?.fields.length > 0 && bpContext.selectedBlueprintComponent?.name ? '' : 'none',
							}}
						/>
						{!isNew && (
							<PermissionWrapper permissions={[po.blueprints.components.delete]}>
								<Button light title={isRename ? 'Back' : 'Rename'} clicked={() => setIsRename(!isRename)}
									titleStyle={{ fontSize: '14px' }}
									style={{
										width: '20%',
										display: bpContext?.selectedBlueprintComponent?.readonly == true && companyData.status !== 'superadmin' ? 'none' : ''
									}} />
							</PermissionWrapper>
						)}
						{!isNew && !isRename && (
							<PermissionWrapper permissions={[po.blueprints.components.delete]}>
								<Button light title={'Duplicate'} clicked={onDuplicateBlueprintComponentPressed}
									titleStyle={{ fontSize: '14px' }}
									style={{
										width: '22%',
									}} />
							</PermissionWrapper>
						)}
						{!isNew && !isRename && bpContext?.selectedBlueprintComponent?.readonly == false && (
							<>
								<PermissionWrapper permissions={[po.blueprints.components.delete]}>
									<Button light title={'New'} clicked={onNewBlueprintComponentPressed}
										titleStyle={{ fontSize: '14px' }}
										style={{
											width: '16%',
										}} />
									{components.filter(com => com.component_fields.filter(cf => cf.component_blueprint_id === bpContext.selectedBlueprintComponent?.id).length > 0).length === 0 &&
										<Button light title={'Delete'} loading={dLoading} clicked={onDeleteComponentBlueprint}
											titleStyle={{ fontSize: '14px' }}
											style={{
												width: '20%',
											}} />}
								</PermissionWrapper>
							</>
						)}

					</div>
				</div>
			</ModalR>


			{sixthModalVisible && <TutorialBits style={{ top: '5%', right: '5%', width: '300px' }}
				title={'Step 7: Create Customer Listing Component'}
				message={`Start by typing Customer Listing in the Name input box. After typing the name let's create several fields.\n In the Fields Selection, click the box, a drop down list should appear, look for and click Text in Input. Text should appear in Selected Fields. Click the settings icon and 
										change the Input name for Text to Customer Name then Save. Create another Text field, name it Customer ID.`}
			/>}
			{seventhModalVisible && <TutorialBits style={{ top: '5%', right: '5%', width: '300px' }}
				title={'Step 9: Create the Customer Details Component'}
				message={`Start by typing Customer Detail in the Name input box. After typing the name, create several fields.\n In the Fields Selection, look for and click Text in Input. Change the input name for Text to Customer Name then Save. Create two more Text fields, with the labels Address and Email. Select a fourth field, Date, name it Date Founded. Finally add a Customer ID field, click settings and add prefix ID of CUST-. Now click Save to finalize this component.`}
				
			/>}

			{optionsModalVisible && (
				<FieldOptionsModal
					onClose={onOptionsModalClose}
					isOpen={optionsModalVisible}
					modalStyle={{ maxWidth: '500px', minWidth: '422px' }}
					leftControlAction={fieldId => onDeleteSelectedComponentBlueprintSelectedField(fieldId)}
					leftControlLoading={processingDelete}
					centralControlAction={onSaveComponentField}
					centralControlLoading={processingUpdate}
					allModules={allModules}
					blueprints={blueprints}
					components={components}
					companyProcesses={companyProcesses}
					products={products}
					selectedFields={bpContext.selectedBlueprintComponent.fields}
					readOnly={bpContext?.selectedBlueprintComponent?.readonly && companyData.status !== 'superadmin'}
				/>
			)}
			{showTutorial && <TutorialRectangle onClick={() => setShowTutorial(false)} url={'https://www.youtube.com/watch?v=xV-bOHvfbPQ'} />}
		</>
	);
});
const RenderFieldContent = props => {
	const { id, value, field_type } = props;
	const { companyData } = store.getState().flowReducer;
	const [, setBpContext] = useBlueprintComponentsContext();

	switch (field_type?.data_type) {
		case 'file':
			return (
				<input
					type={'input'}
					placeholder={field_type?.name}
					className={cls.fieldTypeInput}
					value={value}
					name={field_type?.name}
					onChange={e => {
						setBpContext({ type: bcat.setSelectedBlueprintComponentSpecificField, value: { id: id, value: e.target.value } });
					}}
				/>
			);
		case 'text':
			return (
				<input
					type={field_type?.data_type}
					placeholder={'Input name for ' + field_type?.name}
					className={cls.fieldTypeInput}
					value={value}
					name={field_type?.name}
					onChange={e => {
						let newNumber = e.target.value;
						setBpContext({ type: bcat.setSelectedBlueprintComponentSpecificField, value: { id: id, value: newNumber } });
					}}
				/>
			);
		case 'output':
			return (
				<input
					type={field_type?.data_type}
					placeholder={'Insert value for ' + field_type?.name?.toLowerCase() + ' element...'}
					className={cls.fieldTypeInput}
					value={value}
					name={field_type?.name}
					readOnly={companyData?.id === '-' ? false : field_type?.name === 'Template' ? true : false}
					onChange={e => {
						let newNumber = e.target.value;
						setBpContext({ type: bcat.setSelectedBlueprintComponentSpecificField, value: { id: id, value: newNumber } });
					}}
				/>
			);
		case 'date':
			return (
				<input
					type={'input'}
					placeholder={field_type?.name}
					className={cls.fieldTypeInput}
					value={value}
					name={field_type?.name}
					onChange={e => {
						setBpContext({ type: bcat.setSelectedBlueprintComponentSpecificField, value: { id: id, value: e.target.value } });
					}}
				/>
			);
		default:
			return (
				<input
					type={field_type?.data_type}
					placeholder={'Insert value for ' + field_type?.name?.toLowerCase() + ' element...'}
					className={cls.fieldTypeInput}
					value={value}
					name={field_type?.name}
					onChange={e => {
						let newNumber = e.target.value;
						setBpContext({ type: bcat.setSelectedBlueprintComponentSpecificField, value: { id: id, value: newNumber } });
					}}
				/>
			);
	}
};
const BPFieldRowWrapper = props => {
	const { id, renderError, fieldIcon, children, hasSettingsBtn, settingsClicked, name } = props;
	const [bpContext] = useBlueprintComponentsContext();
	const [showErrorState, setShowErrorState] = useState(false);
	const settingErrorState = useCallback(() => setShowErrorState(renderError && bpContext.validation.fields?.some(i => i?.toString() === id?.toString())), [renderError, bpContext.validation.fields, id]);

	useEffect(() => {
		settingErrorState();
	}, [renderError, id, bpContext.validation.fields, settingErrorState]);
	return (
		<div className={cls.fieldRow} app-field-row-has-error={showErrorState ? 'error' : ''}>
			<div className={cls.fieldRowContent}>{children}</div>
			<p className={cls.typeText}>{name}</p>
			{hasSettingsBtn && (
				<button className={cls.fieldRowControls} onClick={() => settingsClicked()}>
					<FontAwesomeIcon icon={faGear} style={{ width: '20px', height: '20px', color: 'gray' }} />
				</button>
			)}
		</div>
	);
};
const mapStateToProps = store => {
	const { blueprints, allStatutes, selectedBlueprintComponent, allModules, componentTypes, components, products, companyData, companyProcesses } = store.flowReducer;
	const { onBoardingModalsVisibility, isOnboarding } = store.modalsReducer;
	return { blueprints, allStatutes, selectedBlueprintComponent, allModules, componentTypes, components, products, companyData, companyProcesses, onBoardingModalsVisibility, isOnboarding };
};
const mapDispatchToProps = dispatch => {
	return {
		onSetSelectedProStep: selectedStep => dispatch(setSelectedProStep(selectedStep)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(ComponentsModal);
